@import "./node_modules/sass-extras/index";

/*
  1. Use a more-intuitive box-sizing model.
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}
/*
  2. Remove default margin
*/
* {
  margin: 0;
}
/*
  Typographic tweaks!
  3. Add accessible line-height
  4. Improve text rendering
*/
body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
}
/*
  5. Improve media defaults
*/
img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  object-fit: cover;
}
/*
  6. Remove built-in form typography styles
*/
input,
button,
textarea,
select {
  font: inherit;
}
/*
  7. Avoid text overflows
*/
p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
  font: inherit;
}
/*
  8. Create a root stacking context
*/
#root,
#__next {
  isolation: isolate;
}
ul {
  list-style: none;
  margin: 0;
  padding: 0;
}
body {
  font-family: "Raleway", sans-serif;
  font-size: 18px;
  color: #2c2d35;
  font-weight: 500;
  font-style: normal;
  font-optical-sizing: auto;

  a {
    text-decoration: none;
    color: inherit;
  }
}

.hero-title {
  font-size: 30px;
  line-height: 100%;
  margin-bottom: 20px;
  span {
    color: #598d66;
    line-height: 150%;
  }
  @media screen and (min-width: 576px) {
    font-size: 36px;
    margin-bottom: 10px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 20px;
    font-size: 40px;
  }
  @media screen and (min-width: 1440px) {
    font-size: 60px;
    line-height: 110%;
  }
}
.section {
  margin: 0 auto;
  width: 320px;
  @media screen and (min-width: 576px) {
    width: 576px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
  }
}
.section-padding {
  padding-top: 60px;
  padding-bottom: 60px;
  @media screen and (min-width: 576px) {
    padding-top: 50px;
    padding-bottom: 50px;
  }
  @media screen and (min-width: 768px) {
    padding-top: 70;
    padding-bottom: 70px;
  }
}
.container {
  padding-left: 15px;
  padding-right: 15px;

  @media screen and (min-width: 576px) {
    padding-left: 33px;
    padding-right: 33px;
  }
  @media screen and (min-width: 768px) {
    padding-left: 39px;
    padding-right: 39px;
  }
  @media screen and (min-width: 1440px) {
    padding-left: 165px;
    padding-right: 165px;
  }
}

.menu-toggle {
  background-color: transparent;
  border: none;
  font-size: 24px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 52px;
  height: 52px;
  cursor: pointer;
  padding: 10px;
  transition: transform 0.3s ease;

  @media screen and (min-width: 567px) {
    display: none;
  }
}
// .menu-toggle.rotate {
//   transform: rotate(90deg);
// }

.menu li:last-child {
  border-bottom: none;
}

.header {
  width: 100%;

  height: 130px;
  background-color: #e1ede6;
  padding-top: 16px;
  padding-bottom: 16px;
  @media screen and (min-width: 567px) {
    height: 123px;
  }

  @media screen and (min-width: 768px) {
    padding-top: 33px;
    padding-bottom: 33px;
    height: 100px;
  }
}
.navigation {
  margin: 0 auto;
  display: flex;
  position: relative;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
  width: 320px;
  @media screen and (min-width: 567px) {
    width: 567px;
  }
  @media screen and (min-width: 768px) {
    flex-direction: row;
    width: 768px;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
  }
}
.menu {
  display: none;
  flex-direction: row;
  gap: 60px;
  @media screen and (min-width: 576px) {
    display: flex;
  }
}
.menu_wrapper {
  display: flex;
  width: 100%;
  height: 30px;
  align-items: center;
  justify-content: space-between;
  margin-top: 16px;
  @media screen and (min-width: 768px) {
    margin-top: 0;
    padding-top: 0;
    gap: 60px;
  }
  @media screen and (min-width: 1440px) {
    justify-content: flex-end;
    gap: 60px;
  }
}
.menu-item {
  font-size: 18px;
  line-height: 21px;
  &:hover,
  &:focus,
  &:active {
    color: #376b44;
  }
  @media screen and (min-width: 1440px) {
    font-size: 20px;
    line-height: 23px;
  }
}

.logo__header-wrapper {
  width: 100%;
  display: flex;
  justify-content: center;
  border-bottom: 1px solid #c3d1c6;
  padding-bottom: 16px;
  @media screen and (min-width: 768px) {
    padding-bottom: 0;
    border: 0;
    width: fit-content;
    margin-right: 101px;
    padding-top: 0;
  }
  @media screen and (min-width: 1440px) {
    margin-right: 0;
  }
}
.logo-link {
  cursor: pointer;
  width: 143px;
  height: 35px;
}
.menu-mobile.show {
  display: block;
}
.menu-mobile {
  background-color: #e1ede6;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 70px;
  left: 0;
  padding: 16px;
  font-size: 18px;
  line-height: 21px;
  display: none;
}
.menu-mobile__list {
  display: flex;
  flex-direction: column;
  gap: 50px;
}

.menu-mobile__btn {
  width: 24px;
  height: 24px;
  padding: 0;

  background-color: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 30px;
  border: none;
  fill: #2c2d35;
  & svg {
    width: 24px;
    height: 24px;
    &:hover,
    &:focus,
    &:active {
      fill: #376b44;
    }
  }
}

.hero-section {
  padding-top: 60px;

  @media screen and (min-width: 576px) {
    padding-top: 50px;
  }
  @media screen and (min-width: 768px) {
    padding-top: 0;
  }
}
.hero-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
  width: 320px;
  height: 293px;
  @media screen and (min-width: 576px) {
    width: 576px;
    height: 289px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    height: 380px;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
    height: 620px;
  }
}
.hero-image__wrapper {
  display: none;

  @media screen and (min-width: 576px) {
    display: block;
    width: 195px;
    height: 239px;
    background-image: url("./assets/image/heroMd.png");
  }
  @media screen and (min-width: 768px) {
    width: 330px;
    height: 380px;
    background-image: url("./assets/image/heroLg.png");
  }
  @media screen and (min-width: 1440px) {
    width: 540px;
    height: 621px;
    background-image: url("./assets/image/heroXl.png");
  }
}
.hero-info__wrapper {
  width: 100%;
  @media screen and (min-width: 576px) {
    width: 285px;
    height: 239px;
  }
  @media screen and (min-width: 768px) {
    width: 330px;
    height: 308px;
  }
  @media screen and (min-width: 1440px) {
    width: 540px;
    height: 331px;
  }
}
.btn {
  display: flex;
  justify-content: center;
  align-items: center;
  outline: none;
  background-color: transparent;
  width: 100%;
  padding-top: 14.5px;
  padding-bottom: 14.5px;
  font-size: 18px;
  line-height: 21px;
  @media screen and (min-width: 768px) {
    padding-top: 18.5px;
    padding-bottom: 18.5px;
    font-size: 20px;
    line-height: 23px;
  }
  @media screen and (min-width: 1440px) {
    padding-top: 21px;
    padding-bottom: 21px;
  }
}
.btn-hero {
  width: 154px;

  @media screen and (min-width: 576px) {
    width: 178px;
  }
  @media screen and (min-width: 768px) {
    width: 210px;
  }
  @media screen and (min-width: 1440px) {
    width: 227px;
  }
}
.btn-green {
  color: #598d66;
  border: 1px solid #598d66;
  &:hover {
    background-color: #598d66;
    color: white;
  }
  &:active {
    background-color: #376b44;
    &:hover {
      background-color: #598d66;
    }
  }
  &:focus {
    border: 1px solid #598d66;
    background-color: #d4e8d9;
    &:hover {
      background-color: #598d66;
    }
  }
}
.btn-yellow {
  color: #2c2d35;
  width: 257px;
  background-color: #eedb6d;
  border: 1px solid transparent;
  &:hover {
    background-color: #faeb97;
  }
  &:active {
    background-color: #d9b949;
  }
  &:focus {
    background-color: #faeb97;
    border: 2px solid #a38314;
  }
}

.hero-info_text {
  font-size: 14px;
  color: #86928b;

  margin-bottom: 40px;

  @media screen and (min-width: 576px) {
    font-size: 16px;
    margin-bottom: 11px;
  }
  @media screen and (min-width: 768px) {
    font-size: 18px;
    margin-bottom: 40px;
  }
  @media screen and (min-width: 1440px) {
    margin-bottom: 60px;
  }
}
.card-list {
  display: grid;
  grid-template-columns: repeat(1, 1fr);
  gap: 30px;

  @media screen and (min-width: 576px) {
    grid-template-columns: repeat(2, 1fr);
  }
  @media screen and (min-width: 1440px) {
    grid-template-columns: repeat(3, 1fr);
  }
}
.card-list__menu {
  display: flex;
  flex-direction: column;
  gap: 20px;
  margin-bottom: 20px;

  @media screen and (min-width: 768px) {
    flex-direction: row;
    justify-content: space-between;
    margin-bottom: 30px;
  }
}
.card-list__btn-wrapper {
  display: flex;
  flex-direction: row;
  gap: 32px;
  width: 433px;
}
.card-list__title {
  font-size: 24px;
  line-height: 110%;

  @media screen and (min-width: 768px) {
    font-size: 30px;
  }

  @media screen and (min-width: 1440px) {
    font-size: 40px;
  }
}
.card {
  background-color: #e1ede6;
  padding: 20px 20px 30px 20px;
  width: 290px;
  font-size: 18px;

  @media screen and (min-width: 576px) {
    width: 240px;
  }
  @media screen and (min-width: 768px) {
    width: 330px;
  }
  @media screen and (min-width: 1440px) {
    width: 350px;
  }
}
.card__img-wrapper {
  margin-bottom: 20px;
  width: 250px;
  height: 250px;

  img {
    width: 250px;
    height: 250px;
  }

  @media screen and (min-width: 576px) {
    width: 200px;
    height: 250px;
    img {
      width: 200px;
      height: 250px;
    }
  }
  @media screen and (min-width: 768px) {
    width: 290px;
    height: 422px;
    img {
      width: 290px;
      height: 422px;
    }
  }
  @media screen and (min-width: 1440px) {
    width: 310px;
    height: 422px;
    img {
      width: 310px;
      height: 422px;
    }
  }
}
.card__author {
  color: #86928b;
  font-size: 16px;
  line-height: 19px;
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 21px;
  }
}
.card__picture-name {
  font-size: 24px;
  line-height: 28px;
  margin-bottom: 10px;

  @media screen and (min-width: 768px) {
    font-size: 30px;
    line-height: 35px;
    margin-bottom: 8px;
  }
}
.card__description {
  font-size: 16px;
  line-height: 19px;

  margin-bottom: 30px;
  @media screen and (min-width: 768px) {
    font-size: 18px;
    line-height: 21px;
  }
}
.card__price {
  font-size: 20px;
  line-height: 23px;
  margin-bottom: 10px;
  color: #598d66;
  @media screen and (min-width: 768px) {
    font-size: 24px;
    line-height: 28px;
    margin-bottom: 20px;
  }
}
.round-btn {
  font-size: 18px;
  line-height: 21px;
  letter-spacing: 0px;
  padding: 8px 18px;
  background-color: #d4e8d9;
  border: 2px solid transparent;
  border-radius: 20px;
  &:hover {
    background-color: #598d66;
    color: #f4f6f5;
  }
  &:active {
    background-color: #376b44;
    color: #f4f6f5;
  }
  &:focus {
    border: 2px solid #598d66;
    outline: none;
  }
  @media screen and (min-width: 768px) {
    font-size: 20px;
    line-height: 23px;
  }
}
.round-btn.active {
  background-color: #376b44;
  color: #f4f6f5;
  &:hover {
    background-color: #598d66;
  }
}
.promo-section {
  color: #f4f6f5;
  background: linear-gradient(90deg, rgba(44, 45, 53, 1) 0%, rgba(44, 45, 53, 0.5) 100%),
    url("./assets/image/promo_backgroundMd.png");

  width: 320px;
  height: 552px;
  background-size: cover;

  background-repeat: no-repeat;
  padding-top: 50px;
  padding-bottom: 50px;

  @media screen and (min-width: 576px) {
    width: 576px;
    height: 410px;
  }
  @media screen and (min-width: 768px) {
    width: 768px;
    height: 502px;
    background-image: linear-gradient(90deg, rgba(44, 45, 53, 1) 0%, rgba(44, 45, 53, 0.3) 100%),
      url("./assets/image/promo_backgroundLg.png");
    padding-top: 70px;
    padding-bottom: 70px;
  }
  @media screen and (min-width: 1440px) {
    width: 1440px;
    height: 574px;
    background-image: url("./assets/image/promo_backgroundXl.png");
  }
}
.promo__wrapper {
  width: 290px;
  @media screen and (min-width: 576px) {
    width: 510px;
  }
  @media screen and (min-width: 768px) {
    width: 690px;
  }
  @media screen and (min-width: 1440px) {
    width: 540px;
  }
}
.promo__title-wrapper {
  display: flex;
  gap: 20px;
  margin-bottom: 20px;

  width: 290px;
  @media screen and (min-width: 576px) {
    width: 331px;
  }
  @media screen and (min-width: 768px) {
    width: 450px;
  }
  @media screen and (min-width: 1440px) {
    width: 521px;
  }
  & img {
    width: 50px;
    height: 50px;

    @media screen and (min-width: 768px) {
      width: 60px;
      height: 60px;
    }
    @media screen and (min-width: 1440px) {
      width: 80px;
      height: 80px;
    }
  }
}
.promo__title {
  font-size: 24px;
  line-height: 100%;

  @media screen and (min-width: 768px) {
    font-size: 30px;
  }
  @media screen and (min-width: 1440px) {
    font-size: 40px;
  }
}
.promo__text {
  line-height: 140%;
  font-size: 16px;

  margin-bottom: 30px;
  @media screen and (min-width: 576px) {
    margin-bottom: 20px;
  }
  @media screen and (min-width: 768px) {
    margin-bottom: 30px;
    line-height: 150%;
    font-size: 18px;
  }
  @media screen and (min-width: 1440px) {
    margin-bottom: 60px;
  }
}

