.team__wrapper {
  width: 100%;

  @media screen and (min-width: 1440px) {
    width: 635px;
    margin-top: 124px;
  }
}
.team__title {
  font-size: 40px;
  line-height: 110%;
  margin-bottom: 20px;
  @media screen and (min-width: 768px) {
    gap: 30px;
  }
  @media screen and (min-width: 1440px) {
    gap: 20px;
  }
}
.team__text {
  font-size: 18px;
  line-height: 150%;
  margin-bottom: 20px;
}
.team__avatar-wrapper {
  display: flex;
  gap: 30px;
}

